// alert('Hola');
document.addEventListener('DOMContentLoaded',function(){
    iniciarapp();
});

function iniciarapp(){
    navegacionFija();
    crearGaleria();
    scrollNav();
}

function navegacionFija(){
    const barra =  document.querySelector('.header');
    const sobreFestival = document.querySelector('.sobre-festival');
    const body = document.querySelector('body')
    window.addEventListener('scroll', function(){
        // console.log(sobreFestival.getBoundingClientRect());// te da informacion sobre la ubicacion con respecto del elemento que seleccionaste
        if(sobreFestival.getBoundingClientRect().top < 0)
        {
            // console.log('ya pasamos el elemento');
            barra.classList.add('fijo');
            body.classList.add('scroll-body');

        }
        else{
            // console.log('NO hemos pasado el elemento');
            barra.classList.remove('fijo');
            body.classList.remove('scroll-body');



        }
    });
}

function scrollNav(){
    const enlaces = document.querySelectorAll('.navegacion-principal a');
    // console.log(enlaces);
    enlaces.forEach( enlace => {
        enlace.addEventListener('click',function(e){
            e.preventDefault();
            const scrollSection = e.target.attributes.href.value;
            const section = document.querySelector(scrollSection);
            section.scrollIntoView({behavior:"smooth"});
            // console.log(e.target.attributes.href.value);
        })
    });
}

function crearGaleria(){
    const galeria = document.querySelector('.galeria-imagenes');
    // galeria.textContent = 'vamos a crear la galeria';
    for(let i = 1;i <= 12;i++){
        const imagen = document.createElement('picture');
        imagen.innerHTML = 
        `<source  srcset="build/img/thumb/${i}.avif" type="image/avif">
        <source srcset="build/img/thumb/${i}.webp" type="image/webp">
        <img loading="lazy" width="200" height="300" src="build/img/${i}.jpg" alt="imagen galeria">`;
        
        imagen.onclick = function(){//funcion con callback
            mostrarImagen(i);
        }

        galeria.appendChild(imagen);


    }

}

function mostrarImagen(id){
    const imagen = document.createElement('picture');
    imagen.innerHTML = 
    `<source  srcset="build/img/grande/${id}.avif" type="image/avif">
    <source srcset="build/img/grande/${id}.webp" type="image/webp">
    <img loading="lazy" width="200" height="300" src="build/img/${id}.jpg" alt="imagen galeria">`;
    
    //crea el overlay con la imagen
    const overlay = document.createElement('div');
    overlay.appendChild(imagen);
    overlay.classList.add('overlay');
    overlay.onclick = function(){
        overlay.remove();
        const body = document.querySelector('body');
        body.classList.remove('fijarBody');
    }
    //boton cerrar
    const cerrarModal = document.createElement('p');
    cerrarModal.textContent = 'X';
    cerrarModal.classList.add('botonCerrar');
    cerrarModal.onclick = function(){//siempre debe de ir antes de el append del contenedor
        overlay.remove();
        const body = document.querySelector('body');
        body.classList.remove('fijarBody');
    };
    overlay.appendChild(cerrarModal);

    //a√±adir el elemento al html
    const body = document.querySelector('body');
    body.appendChild(overlay);
    body.classList.add('fijarBody');

}